.Home{
    .background{
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    .background > img{
        width: auto;
        height: auto;
        max-height: 100%;
        min-width: 100%;
    }

    .deskArea{
        position: relative;
        width: 100%;
        height: 100%;
    }

    .windowBar{
        width: 100%;
        height: fit-content;
        padding: .4rem .4rem .1rem .4rem;
        border-radius: .4rem .4rem 0 0;

        ul{
            display: flex;
            flex-direction: row-reverse;
            flex-wrap: nowrap;
            justify-content: space-between;
            align-items: center;
            height: 1.7rem;
            margin-top: .1rem;
        }

        .windowBarIcons{
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            justify-content: space-between;
            align-items: center;
            height: 1.7rem;
        }

        ul div{
            margin-right: 3rem;
            margin-left: .4rem;
        }

        .windowIcon{
            margin-right: .4rem;
            font-family: 'josefinsans-semibold';
            font-size: 1rem;
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            align-items: center;
        }

        .windowIcon p{
            width: fit-content;
            height: fit-content;
            padding-bottom: .01rem;
        }

        .windowIcon img{
            width: 1.5rem;
            height: 1.5rem;
            margin-left: .4rem;
        }

        .windowBarIcons img{
            width: 1.5rem;
            height: auto;
            margin-right: .4rem;
        }
    }
}