.Window{
    width: fit-content;
    height: fit-content;
    position: absolute;
    top: 10vh;
    left: 10vw;
    z-index: 3;

    .content{
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        max-height: 100%;
        max-width: 100%;
        overflow: hidden;
        width: fit-content;
        min-width: 7rem;
        min-height: 5rem;
        padding: .8rem;
    }

    .windowContent{
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        position: relative;
        width: fit-content;
        height: fit-content;
        margin-right: 2rem;
        background-color: #f0f0f094;
        box-shadow: -1px -1px 5px #2323236b;
        backdrop-filter: blur(20px);
        border-radius: .4rem;
    }
}